html {
    position: relative;
    min-height: 100%;

    &.popup-opened {
        overflow: hidden;

        @media screen and (max-width: 768px) {
            height: 100vh;
        }
    }
}

/* ********************** MODAL WINDOW ********************************/

.my_overlay {
    @include overlay('');
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.75);
    opacity: 0.9;
    cursor: pointer;
    display: block;
}

.popup {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 5000;
    display: none;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: rgba(0, 0, 0, 0.8);

    &__service {
        background-color: rgba(0, 0, 0, 0.8);

        &-inner {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 40px;
            min-width: 100%;
            max-height: 100vh;
            overflow: auto;

            @media screen and (max-width: 992px) {
                gap: 20px;
            }

            @media screen and (max-width: 768px) {
                padding: 0 !important;
                display: flex;
                flex-direction: column-reverse;
                gap: 0;
            }
        }

        &-image {
            margin: 0;
            overflow: hidden;
            // border-radius: 0 180px 180px 0;
            height: 100%;
            clip-path: url(#svg-clip-87);

            @media screen and (max-width: 768px) {
                border-radius: 100px 100px 0 0;
                height: 200px;
            }

            @media screen and (max-width: 480px) {
                height: 156px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-content {
            padding: 32px 0 40px;

            @media screen and (max-width: 768px) {
                padding: 24px;
            }

            &-wrap {
                padding-left: 78px;

                @media screen and (max-width: 480px) {
                    padding-left: 0;
                }
            }
        }

        &-icon {
            width: 61px;
            flex-shrink: 0;

            @media screen and (max-width: 480px) {
                width: 30px;
            }

            img {
                width: 100%;
                height: auto;
            }
        }

        &-right {
            width: 100%;
        }

        &-title {
            color: #f3f0ec;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 30px;
            font-style: normal;
            font-weight: 700;
            line-height: 1.26;
            margin-bottom: 5px;

            @media screen and (max-width: 992px) {
                font-size: 25px;
            }

            @media screen and (max-width: 768px) {
                font-size: 20px;
            }

            @media screen and (max-width: 480px) {
                font-size: 16px;
            }

            &-wrap {
                display: flex;
                align-items: center;
                gap: 16px;
                margin-bottom: 16px;

                @media screen and (max-width: 480px) {
                    gap: 8px;
                }
            }
        }

        &-subtitle {
            color: #f3f0ec;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: 1.3;
            margin: 0;

            @media screen and (max-width: 992px) {
                font-size: 18px;
            }

            @media screen and (max-width: 768px) {
                font-size: 16px;
            }

            @media screen and (max-width: 480px) {
                font-size: 12px;
            }
        }

        &-text {
            p {
                color: #fff;
                font-feature-settings: 'clig' off, 'liga' off;
                font-family: Turbine, sans-serif;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 1.42;
                margin-bottom: 24px;

                @media screen and (max-width: 768px) {
                    font-size: 14px;
                }
            }

            ul {
                padding: 0;
                margin-bottom: 24px;

                li {
                    color: #f3f0ec;
                    font-feature-settings: 'clig' off, 'liga' off;
                    font-family: Turbine, sans-serif;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 20px;
                    position: relative;
                    list-style: none;
                    margin-bottom: 0;
                    padding-left: 15px;

                    @media screen and (max-width: 768px) {
                        font-size: 14px;

                        br {
                            display: none;
                        }
                    }

                    &:before {
                        content: "";
                        height: 12px;
                        left: -4px;
                        position: absolute;
                        top: 5px;
                        width: 14px;
                        background-size: cover;
                        background-image: url(data:image/svg+xml;base64,);
                    }
                }
            }
        }

        &-link {
            display: inline-block;
            color: #cc7510;
            font-feature-settings: 'clig' off, 'liga' off;
            font-family: Turbine, sans-serif;
            font-size: 14px;
            font-style: normal;
            font-weight: 700;
            text-transform: uppercase;
            line-height: 1.57;
            background-color: #17181a;
            padding: 0 8px;
            text-decoration: none;
            transition: 0.3s;

            @media screen and (min-width: 768px) {
                &:hover {
                    background-color: #43464c;
                }
            }

            @media screen and (max-width: 768px) {
                font-size: 12px;
            }

            @media screen and (max-width: 480px) {
                font-size: 10px;
                line-height: 1.6;
            }
        }
    }

    &__wrapper-inner {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__btn-close {
        position: absolute;
        top: 20px;
        right: 20px;

        svg {
            pointer-events: none;

            @media screen and (max-width: 480px) {
                width: 12px;
                height: 12px;
            }
        }
    }

    &-wrapper-inner {
        position: relative;
        z-index: 5;
    }
}

#popup-subscribe,
#popup-portal-demo,
#popup-request-demo {
    background: none;

    .popup-wrapper-inner {
        background-color: #F3F0EC;
        position: relative;
        z-index: 1;
        max-width: 1080px;
        top: 100px;
        left: 50%;
        transform: translateX(-50%);

        @media screen and (max-width: 1280px) {
            top: 50px;
            max-width: 980px;
        }

        @media screen and (max-width: 1199px) {
            max-width: 890px;
        }

        @media screen and (max-width: 1030px) {
            max-width: 845px;
        }

        @media screen and (max-width: 991px) {
            max-width: 800px;
        }

        @media screen and (max-width: 850px) {
            max-width: 90%;
        }

        @media screen and (max-width: 480px) {
            top: 20px;
        }
    }

    .subscribe-form,
    .portal-demo,
    .request-demo {

        &__top,
        &__form-content-wrap {
            padding-left: 80px;
            padding-right: 80px;

            @media screen and (max-width: 1280px) {
                padding-left: 60px;
                padding-right: 60px;
            }

            @media screen and (max-width: 1030px) {
                padding-left: 40px;
                padding-right: 40px;
            }

            @media screen and (max-width: 850px) {
                padding-left: 24px;
                padding-right: 24px;
            }

            @media screen and (max-width: 480px) {
                padding-left: 8px;
                padding-right: 8px;
            }
        }

        &__top {
            padding-top: 20px;
            padding-bottom: 20px;
            background: #151836;
            display: flex;
            align-items: center;
            justify-content: flex-start;

            @media screen and (max-width: 1030px) {
                padding-bottom: 15px;
                padding-top: 15px;
            }

            @media screen and (max-width: 670px) {
                padding-bottom: 10px;
                padding-top: 10px;
            }

            @media screen and (max-width: 480px) {
                padding-bottom: 8px;
                padding-top: 8px;
            }
        }

        &__form-content-wrap {
            padding-top: 40px;
            padding-bottom: 40px;

            @media screen and (max-width: 1030px) {
                padding-top: 30px;
            }

            @media screen and (max-width: 991px) {
                padding-top: 24px;
                padding-bottom: 24px;
            }

            @media screen and (max-width: 480px) {
                padding-bottom: 16px;
            }
        }

        &__logo {
            width: 47px;
            height: 47px;
            display: flex;
            align-items: center;
            justify-content: center;

            @media screen and (max-width: 1030px) {
                height: 37px;
                width: 37px;
            }

            @media screen and (max-width: 670px) {
                height: 32px;
                width: 32px;
            }

            @media screen and (max-width: 480px) {
                height: 29px;
                width: 29px;
            }

            img {
                width: 100%;
                height: auto;
            }
        }

        &__logo-title {
            padding-left: 36px;
            position: relative;
            z-index: 1;
            color: #F3F0EC;
            font-family: 'Turbine', sans-serif;
            font-size: 20px;
            font-style: normal;
            font-weight: 700;
            line-height: 1.4;
            text-transform: uppercase;
            padding-right: 50px;

            @media screen and (max-width: 1280px) {
                font-size: 18px;
            }

            @media screen and (max-width: 1030px) {
                font-size: 16px;
            }

            @media screen and (max-width: 850px) {
                font-size: 14px;
            }

            @media screen and (max-width: 480px) {
                font-size: 12px;
                padding-left: 16px;
            }

            &:after {
                position: absolute;
                content: '';
                display: block;
                left: 18px;
                top: 50%;
                transform: translateY(-50%);
                width: 1px;
                height: 47px;
                z-index: 1;
                background-color: #F3F0EC;

                @media screen and (max-width: 1030px) {
                    height: 37px;
                }

                @media screen and (max-width: 670px) {
                    height: 32px;
                }

                @media screen and (max-width: 480px) {
                    height: 29px;
                    left: 8px;
                }
            }
        }

        &__main-img {
            width: 100%;
            height: 450px;
            margin-bottom: 0;

            @media screen and (max-width: 1280px) {
                height: 405px;
            }

            @media screen and (max-width: 1199px) {
                height: 365px;
            }

            @media screen and (max-width: 1030px) {
                height: 345px;
            }

            @media screen and (max-width: 991px) {
                height: 325px;
            }

            @media screen and (max-width: 768px) {
                height: 285px;
            }

            @media screen and (max-width: 670px) {
                height: 250px;
            }

            @media screen and (max-width: 570px) {
                height: 210px;
            }

            @media screen and (max-width: 480px) {
                height: 180px;
            }

            @media screen and (max-width: 415px) {
                height: 150px;
            }

            @media screen and (max-width: 375px) {
                height: 142px;
            }

            @media screen and (max-width: 330px) {
                height: 120px;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: 50% 50%;
            }
        }

        &__form-title {
            color: #2F3034;
            margin-bottom: 24px;

            @media screen and (max-width: 480px) {
                text-align: center;
                max-width: 330px;
                margin-left: auto;
                margin-right: auto;
            }
        }

        &__form-des {
            margin-bottom: 40px;
            max-width: 385px;

            @media screen and (max-width: 480px) {
                text-align: center;
                line-height: 1.12;
            }
        }
    }

    .wpcf7-submit,
    input[type="submit"] {
        @extend %orange-button;
        text-transform: uppercase;
    }

    .inputs-submit-container {
        margin-top: 32px;
    }

    .hbspt-form .hs-error-msgs label {
        color: red !important;
        font-size: 14px !important;
        margin: 0 !important;
    }

    .hbspt-form .hs-error-msgs {
        margin: 6px 0;
        padding: 0;
    }

    .hbspt-form form {
        gap: 0;
        margin-bottom: 0;
    }

    .hs_error_rollup {
        display: none;
    }

    .hbspt-form .hs-form-field label {
        color: #2f3034;
        display: inline-block;
        font-family: Turbine, sans-serif;
        font-size: 30px;
        font-style: normal;
        font-weight: 300;
        margin-bottom: 16px;
        text-transform: lowercase;

        @media screen and (max-width: 670px) {
            font-size: 20px;
            margin-bottom: 10px;
        }

        @media screen and (max-width: 480px) {
            font-size: 18px;
        }
    }

    .hs_submit.hs-submit {
        width: max-content;
        margin-top: 32px;

        @media screen and (max-width: 768px) {
            margin-top: 24px;
        }

        @media screen and (max-width: 480px) {
            display: flex;
            width: 100%;
        }

        .actions {
            @media screen and (max-width: 480px) {
                margin-left: auto;
                margin-right: auto;
            }

            input[type="submit"] {
                @media screen and (max-width: 480px) {
                    font-size: 13px;
                }
            }
        }
    }

    .popup-close {
        position: absolute;
        z-index: 1;
        right: 80px;
        top: 33px;
        width: 22px;
        height: 22px;
        background: transparent;
        font-size: 0;
        border: 0;
        appearance: none;

        @media screen and (max-width: 1280px) {
            right: 60px;
        }

        @media screen and (max-width: 1030px) {
            right: 40px;
            top: 23px;
        }

        @media screen and (max-width: 850px) {
            right: 24px;
        }

        @media screen and (max-width: 670px) {
            top: 16px;
            right: 22px;
        }

        @media screen and (max-width: 570px) {
            top: 14px;
        }

        @media screen and (max-width: 480px) {
            right: 8px;
            top: 15px;
            height: 12px;
            width: 12px;
        }

        &:after {
            position: absolute;
            content: '';
            display: block;
            right: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
            background-size: contain;
        }
    }

    h3 {
        &.subscribe-form__form-title {
            @media screen and (max-width: 480px) {
                font-size: 20px;
                line-height: 1.4;
            }

            @media screen and (max-width: 320px) {
                font-size: 18px;
            }
        }
    }
}

#popup-portal-demo,
