    .portal-demo,
    .request-demo {

        &__wrapper {
            background-repeat: no-repeat;
            background-size: cover;
            background-position: 50% 50%;
        }

        &__top,
        &__form-content-wrap {

        }

        &__top {
            background: #F3F0EC;
        }

        &__form-content-wrap {
            position: relative;
            z-index: 1;

            &:after {
                position: absolute;
                content: '';
                display: block;
                left: 0;
                top: 0;
